@font-face {
  font-family: "lato";
  src: url("@/config/client/fonts/Lato/Lato-Regular.ttf") format("truetype"); }

/*
    Window StyleSheet
*/
.window {
  display: flex; }

.window-content {
  border: 2.5px solid white;
  border-radius: 5px;
  background: linear-gradient(148deg, rgba(79, 82, 136, 0.7) 0%, rgba(42, 43, 73, 0.7) 100%);
  padding: 20px;
  overflow: hidden; }

.window.top {
  align-items: flex-start; }

.window.bottom {
  align-items: flex-end; }

.window.bottom-middle {
  align-items: flex-end;
  justify-content: center;
  width: 100%; }

.window.middle {
  align-items: center;
  justify-content: center; }

.window-content.full-width {
  position: relative;
  width: 100%; }

.fade-enter-active, .fade-leave-active {
  transition: opacity .3s; }

.fade-enter, .fade-leave-to {
  opacity: 0; }
