@font-face {
  font-family: "lato";
  src: url("@/config/client/fonts/Lato/Lato-Regular.ttf") format("truetype"); }

/*
    Window StyleSheet
*/
@keyframes cursor {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 0.7; } }

.shop-menu {
  display: flex;
  position: absolute;
  width: 100%;
  z-index: 100; }

.shop-content {
  height: calc(100% - 190px);
  display: flex; }

.shop-content > div:first-child {
  width: calc(100% - 300px);
  padding: 20px;
  padding-left: 0; }

.shop-content > div:last-child {
  width: 300px;
  padding: 10px; }

.shop-info {
  border-left: 1px solid white; }

.space-between {
  justify-content: space-between;
  display: flex; }

hr {
  margin: 20px 0px; }

.row {
  flex-direction: row;
  display: flex; }

.row > * {
  flex-direction: column;
  flex-flow: row wrap; }

.row > div:last-child {
  text-align: right;
  width: 100%;
  padding: 10px; }

.can-not-buy {
  opacity: 0.5; }

.item {
  margin: 0;
  position: relative;
  padding: 10px; }

.item-quantity {
  display: flex;
  align-items: center; }

.item-quantity > div {
  width: 100%;
  padding: 0px 20px; }

.total {
  border-top: 1px solid white;
  margin-top: 14px;
  padding: 10px;
  padding-right: 0; }

.cursor {
  position: relative; }

.cursor > * {
  z-index: 10;
  padding: 10px;
  position: relative; }

.cursor:before {
  content: '';
  position: absolute;
  background: #7782ab;
  width: 100%;
  height: 100%;
  left: 0px;
  border: 1px solid #9db0c6;
  animation: cursor 0.6s infinite alternate ease-in-out;
  z-index: 0; }
