@font-face {
  font-family: "lato";
  src: url("@/config/client/fonts/Lato/Lato-Regular.ttf") format("truetype"); }

/*
    Window StyleSheet
*/
@keyframes cursor {
  0% {
    opacity: 0.4; }
  100% {
    opacity: 0.7; } }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

ul li {
  margin-bottom: 10px;
  position: relative; }

ul li.active {
  display: block; }

ul li > * {
  padding: 10px; }

ul li.active:before {
  content: '';
  position: absolute;
  background: #7782ab;
  width: 100%;
  height: 100%;
  left: 0px;
  border: 1px solid #9db0c6;
  animation: cursor 0.6s infinite alternate ease-in-out;
  z-index: 0; }

p {
  margin: 0;
  position: relative;
  padding: 10px; }
