@font-face {
  font-family: "lato";
  src: url("@/config/client/fonts/Lato/Lato-Regular.ttf") format("truetype"); }

/*
    Window StyleSheet
*/
.alert {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: "lato";
  padding: 15px 35px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  transition: 0.5s all ease;
  max-width: 400px; }
  .alert.bottom {
    transform: translateY(50px);
    margin-top: auto; }
    .alert.bottom.show {
      transform: translateY(-10px); }
  .alert.top {
    transform: translateY(-50px); }
    .alert.top.show {
      transform: translateY(10px); }
  .alert.error {
    background-color: rgba(181, 64, 64, 0.7); }
  .alert.success {
    background-color: rgba(40, 121, 30, 0.7); }

.alert-panel {
  display: flex;
  align-items: center;
  justify-items: flex-end;
  flex-direction: column;
  position: absolute;
  width: 100%;
  overflow: hidden;
  z-index: 100; }
  .alert-panel.bottom {
    height: 100%; }
  .alert-panel.top {
    padding-bottom: 10px; }

.icon img {
  width: 55px;
  margin-right: 10px;
  fill: red !important; }
