@import '@/config/client/theme.scss';


.menu-row {
    flex-direction: row;
    display: flex;
    height: 100%;
}

.menu-left {
    width: 320px;
    display: flex;
    flex-direction: column;
    flex-flow: row wrap;
}

.menu-choice {
    align-items: flex-start;
    width: 100%;
}

.menu-left .gold {
    align-items: flex-end;
    width: 100%;
}

.menu-right {
    width: 100%;
    margin-left: 1px;
}
